import React from 'react';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';

const profile = ({ id, this: love}) => (
    <Panel id={id}>
        <div className="profile">
        <div className="titlesbiography"  onClick={() => love.goBack()}>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Назад

        </div>
        <br/>
        <br/>
        {love.state.booksfavorite_array.length === 0 ? 
        
        love.state.bookshistory_array.length === 0 && love.state.booksfavorite_array.length === 0? <div className="errorprofile">У вас нет избранных и истории книг!</div> : null
        : null}
        {love.state.booksfavorite_array.length === 0 ? 
        <div style={{marginTop: "-35px"}}></div>
        :         <div className="history">
        <div className="titless">
            Избранное
        </div>
        </div> }
                <br/>
            {love.state.booksfavorite_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                    <div className="allbooks">
                                        <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                                        <div onClick={() => love.goBook(result["id"])} className="content">
                                        <div className="contenttitle">{result['name']}</div>
                                        <div className="contentreyt">{result['rating']}</div>
                                        <div className="contentauthorhome">
                                        {result['authors'].map(function(result, i) {
                                return (
                                <span key={i}>
                                {result['first_name']} {result['last_name']} 
                                </span>
                                )
                        }) }
                                        </div>
                                        <div className="contentviews">
                                        Жанр: {result['genres'].map(function(result, i) {
                                return (
                                <span key={i}>
                                     {result['name']},
                                </span>
                                )
                        }) }
                        <div className="contentviews">
                                            Просмотров: {result['views']}
                                        </div>
                                        </div>
                                    </div>
                                    <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], book_id_forshare: result['id']}); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                            </div>
                            </div>
                                )
                    })}
                    <br/>
{love.state.bookshistory_array.length === 0 ? 
null 
        :         <div className="history">
        <div className="titless">
            История <img onClick={() => love.deleteHistory()} className="Iconin" src="https://img.icons8.com/ios-filled/50/000000/broom.png"/>
        </div>
        </div>   }
            {love.state.bookshistory_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                    <div className="allbooks">
                                        <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                                        <div onClick={() => love.goBook(result["id"])} className="content">
                                        <div className="contenttitle">{result['name']}</div>
                                        <div className="contentreyt">{result['rating']}</div>
                                        <div className="contentauthorhome">
                                        {result['authors'].map(function(result, i) {
                                return (
                                <span key={i}>
                                {result['first_name']} {result['last_name']} 
                                </span>
                                )
                        }) }
                                        </div>
                                        <div className="contentviews">
                                        Жанр: {result['genres'].map(function(result, i) {
                                return (
                                <span key={i}>
                                     {result['name']},
                                </span>
                                )
                        }) }
                        <div className="contentviews">
                                            Просмотров: {result['views']}
                                        </div>
                                        </div>
                                    </div>
                                    <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                            </div>
                            </div>
                                )
                    })}
        </div>
    </Panel>
);

export default profile;